// export default Top;
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {Link} from 'react-scroll';

function Top() {
  return (
    <Navbar expand="lg" className='nvb'>
      <Container>
        <Navbar.Brand href="/">MoviesToWatch</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Link className="nav-link" activeClass="active" to="about" spy={true} smooth={true}>About</Link>
            {/* <Nav.Link href="/" to="about">Home</Nav.Link> */}
            <Link className="nav-link" activeClass="active" to="footer" spy={true} smooth={true}>Project Developer</Link>
            {/* <Nav.Link href="/" to="footer">Project Developer</Nav.Link> */}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Top;